import React, { useEffect, useState } from 'react';
import Bootcamp from './Bootcamp';

const Bootcamps = () => {
    const [bootcamps, setBootcamps] = useState([]);

    useEffect(() => {
        fetchBootcamps();
    }, [])

    const fetchBootcamps = async () => {
        const response = await fetch('http://127.0.0.1:8000/api/v1/bootcamps/');
        const data = await response.json();
        console.log(data);
        setBootcamps(data['results']);
    }

    const bootcampsList = bootcamps.map(bootcamp => {
        return <Bootcamp key={bootcamp.id} name={bootcamp.name} careers={bootcamp.careers} photo={bootcamp.photo} />
    })

    return (
        <div>
            {bootcampsList}
        </div>
    )
}

export default Bootcamps;
