import React from 'react'

const Bootcamp = (props) => {
    return (
        <div className="card mb-3">
            <div className="row no-gutters">
                <div className="col-md-4">
                    <img src={`http://127.0.0.1:8000${props.photo}`} className="card-img" alt="..." />
                </div>
                <div className="col-md-8">
                    <div className="card-body">
                        <h5 className="card-title">
                            <a href="bootcamp.html">{props.name}<span className="float-right badge badge-success">7.5</span></a>
                        </h5>
                        <span className="badge badge-dark mb-2">Boston, MA</span>
                        <p className="card-text">
                            {props.careers.map(career => career.name).join(', ')}
                        </p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Bootcamp
